<template>
  <div class="">
    <h2></h2>
    <p></p>
    <h3></h3>
    <p></p>
    <div class="number-ct">
      <jo-count 
        :rule="numberCount" 
        @count-change="updateCount"
        @foucs-change="updateFoucs"></jo-count>
    </div>
    <jo-button type="main" @click.left="numberCount.disable = !numberCount.disable"></jo-button>
    <p></p>

    <pre>
      <code class="language-html">
        
        &lt;template&gt;
          &lt;jo-count :rule="numberCount" @count-change="updateCount"&gt;&lt;/jo-count&gt;
        &lt;/template&gt; 

        &lt;script&gt;
          export default {
            data() {
              return {
                numberCount: {
                  disable: false,
                  value: 0,
                  min: 0,
                  max: 100,
                  customClass: "foo"
                }
              }
            }
          }
        &lt;/script&gt;

      </code>
    </pre>
    <h3>Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>disable</td>
        <td></td>
        <td>boolean</td>
        <td>false</td>
      </tr>
      <tr>
        <td>value</td>
        <td></td>
        <td>number</td>
        <td>0</td>
      </tr>
      <tr>
        <td>min</td>
        <td></td>
        <td>number</td>
        <td>0</td>
      </tr>
      <tr>
        <td>max</td>
        <td></td>
        <td>number</td>
        <td>100</td>
      </tr>
      <tr>
        <td>customClass</td>
        <td> </td>
        <td>string</td>
        <td></td>
      </tr>
    </table>

    <h3></h3>
    <p>1.number</p>
    <p>2.</p>
    <p>3.</p>
  </div>
</template>

<script>
    'use strict'
    export default {
        name: 'NumberCount',
        components: {

        },
        data() {
            return {
              numberCount: {
                disable: false,
                value: 1,
                min: 0,
                max: 100,
                customClass: "foo"
              }
            }
        },
        created() {
            HlInit()
        },
        ready () {

        },
        methods : {
          updateFoucs (status) {
            this.$message({
              type: "info",
              message: "" + (status ? "" : "") + ""
            });
          },
          updateCount (number) {
            this.$message({
              type: "success",
              message: "" + number
            });
            this.numberCount.value = number;
          }
        }
    }
</script>
<style scoped>
  .number-ct {
    margin: 20px auto;
  }
</style>



