<template>
    <div class="">
        <h2></h2>
        <p></p>
        <h3></h3>
        <p></p>
        <p>
            <jo-switch :open="open" @click="open=!open"></jo-switch>
        </p>
        <p></p>
        <pre>
            <code class="language-html">
            &lt;template&gt;
                &lt;jo-switch :open="open" @click="open=!open"&gt;&lt;/jo-switch&gt;
            &lt;/template&gt;

            &lt;script&gt;
                export default {
                    data() {
                        return {
                            open: false
                        }
                    }
                }
            &lt;/script&gt;
            </code>
        </pre>
        <h3></h3>
        <table>
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>open</td>
                <td></td>
                <td>boolean</td>
                <td></td>
                <td>false</td>
            </tr>
        </table>
    </div>
</template>

<script>
    'use strict'

    export default {
        name: 'switch',
        components: {

        },
        data() {
            return {
                open: false
            }
        },
        created() {
            HlInit()
        }
    }
</script>



