<template>
  <div class="">
    <h2></h2>
    <p></p>

    <h3></h3>
    <jo-time-picker  
        v-model="time1"
        size="small"
        @change="updateTime"
       :picker-options="{start: '00:00', end: '24:00',step: '00:30'}"
        placeholder="">
    </jo-time-picker>

    <h3>format</h3>
    <jo-time-picker  
        v-model="time1"
        size="small"
        format='HH:mm'
        @change="updateTime"
       :picker-options="{start: '00:00', end: '24:00',step: '00:30'}"
        placeholder="">
    </jo-time-picker>

    <p></p>
  
    <pre>
        <code class="language-html">

          &lt;template&gt;
            &lt;jo-time-picker  
                v-model="time"
                format='HH:mm'
                @change="updateTime"
                :picker-options="{start: '00:00', end: '24:00',step: '00:30'}"
                placeholder=""&gt;
            &lt;/jo-time-picker&gt;
          &lt;/template&gt; 

          &lt;script&gt;
            export default {
              data() {
                return {
                  time: "",
                  value: Date.now()
                }
              },
              created() {
                HlInit();
              },
              methods: {
                updateTime (data) {
                  this.$message({
                    type: "success",
                    message: "" + data
                  })
                }
              }
            }
          &lt;/script&gt;
  
        </code>
    </pre>

    <h3></h3>
    <p><strong>selectableRange</strong></p>
    <jo-time-picker  
        v-model='time2'
        size='small'
        format='HH:mm:ss'
       :picker-options="{ 
         selectableRange: '00:00:00 - 24:00:00',
         format: 'HH:mm:ss'
       }"
       @change = "updateTime"
        placeholder="">
    </jo-time-picker>

     <p></p>
  
    <pre>
        <code class="language-html">

          &lt;template&gt;
            &lt;jo-time-picker  
                v-model="time"
                @change="updateTime"
                :picker-options="{start: '00:00', end: '24:00',step: '00:30'}"
                placeholder=""&gt;
            &lt;/jo-time-picker&gt;
          &lt;/template&gt; 

          &lt;script&gt;
            export default {
              data() {
                return {
                  time: "",
                  value: Date.now()
                }
              },
              created() {
                HlInit();
              },
              methods: {
                updateTime (data) {
                  this.$message({
                    type: "success",
                    message: "" + data
                  })
                }
              }
            }
          &lt;/script&gt;
  
        </code>
    </pre>

    <h3></h3>
    <p><strong>is-range</strong></p>

    <jo-time-picker  
        v-model="time5"
        is-range
        @change = "updateTime"
        placeholder="">
    </jo-time-picker>


   

    <h3>Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>readonly</td>
        <td></td>
        <td>boolean</td>
        <td></td>
        <td>false</td>
      </tr>
      <tr>
        <td>disabled</td>
        <td></td>
        <td>boolean</td>
        <td></td>
        <td>false</td>
      </tr>
      <tr>
        <td>editable</td>
        <td></td>
        <td>boolean</td>
        <td></td>
        <td>true</td>
      </tr>
      <tr>
        <td>clearable</td>
        <td></td>
        <td>boolean</td>
        <td></td>
        <td>true</td>
      </tr>
      <tr>
        <td>size</td>
        <td></td>
        <td>string</td>
        <td>large, small, mini	</td>
        <td></td>
      </tr>
      <tr>
        <td>placeholder</td>
        <td></td>
        <td>string</td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>value</td>
        <td></td>
        <td>TimePicker: DateTimeSelect: String</td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>align</td>
        <td></td>
        <td>string</td>
        <td>left, center, right</td>
        <td>left</td>
      </tr>
      <tr>
        <td>popper-class</td>
        <td>TimePicker </td>
        <td>string</td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>picker-options</td>
        <td></td>
        <td>object</td>
        <td></td>
        <td>{}</td>
      </tr>
    </table>

    <h3>Time Select Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>start</td>
        <td></td>
        <td>string</td>
        <td></td>
        <td>09:00</td>
      </tr>
      <tr>
        <td>end</td>
        <td></td>
        <td>string</td>
        <td></td>
        <td>18:00</td>
      </tr>
      <tr>
        <td>step</td>
        <td></td>
        <td>string</td>
        <td></td>
        <td>00:30</td>
      </tr>
      <tr>
        <td>minTime</td>
        <td></td>
        <td>string</td>
        <td></td>
        <td>00:00</td>
      </tr>
      <tr>
        <td>maxTime</td>
        <td></td>
        <td>string</td>
        <td></td>
        <td></td>
      </tr>
    </table>

    <h3>Time Picker Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>selectableRange</td>
        <td>'18:30:00 - 20:30:00'['09:30:00 - 12:00:00', '14:30:00 - 18:30:00']</td>
        <td>string/array</td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>format</td>
        <td>(TimePicker)</td>
        <td>string</td>
        <td>HHmmss</td>
        <td>'HH:mm:ss'</td>
      </tr>
    </table>

    <h3>Events</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>change</td>
        <td>input</td>
        <td>formatted</td>
      </tr>
    </table>
  </div>
</template>

<script>
  'use strict'
  export default {
    name: 'Timepicker',
    data() {
      return {
        size: "small",
        time1: "",
        time2: "",
        time3: "",
        time4: "",
        time5: "",
        value: Date.now()
      }
    },
    created() {
      HlInit();
    },
    methods: {
      updateTime (data) {
        this.$message({
          type: "success",
          message: "" + data
        })
      }
    }
  }
</script>

<style scoped lang="scss">
.top-msg-ct {
  margin: 20px auto;
}
</style>


