<template>
  <div class="">
    <h2></h2>
    <h3></h3>
    <p></p>
    <div class="search-ct">
      <jo-search :search="searchParam"  @search-change="updateSearch"  @confirm-search="confirmSearch" @focus-change="focusChange" ></jo-search>
    </div>
    <jo-button type="main" @click.left="searchParam.placeholder = 'placeholder' ">placeholder</jo-button>
    <jo-button type="main" @click.left="searchParam.max = 20">20</jo-button>
    <p></p>

    <pre>
      <code class="language-html">
        
        &lt;template&gt;
          &lt;jo-search :search="searchParam" 
              @search-change="updateSearch" @confirm-search="confirmSearch" @focus-change="focusChange"&gt;&lt;/jo-search&gt;
        &lt;/template&gt; 

        &lt;script&gt;
          export default {
            data() {
              return {
                searchParam: {
                  placeholder: "",
                  word: "",
                  max: 10
                }
              }
            },
            methods : {
              focusChange (isFocus) {
                this.$message({
                  type: "info",
                  message: '' + (isFocus ? '' : ''),
                  duration: 1000
                });
              },
              confirmSearch (data) {
                this.$message({
                  type: "success",
                  message: '' + data
                });
              },
              updateSearch (word) {
                this.searchParam.word = word;
                this.$message({
                  type: "success",
                  message: ': ' + word
                });
              }
            }
          }
        &lt;/script&gt;

      </code>
    </pre>
    <h3>Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>placeholder</td>
        <td></td>
        <td>srting</td>
        <td></td>
      </tr>
      <tr>
        <td>word</td>
        <td></td>
        <td>string</td>
        <td>""</td>
      </tr>
        <td>max</td>
        <td></td>
        <td>number</td>
        <td>20</td>
      </tr>
    </table>
  </div>
</template>

<script>
    'use strict'
    export default {
      name: 'NumberCount',
      components: {

      },
      data() {
          return {
            searchParam: {
              placeholder: "",
              word: "",
              max: 10
            }
          }
      },
      created() {
          HlInit()
      },
      ready () {

      },
      methods : {
        focusChange (isFocus) {
          this.$message({
            type: "info",
            message: '' + (isFocus ? '' : ''),
            duration: 1000
          });
        },
        confirmSearch (data) {
          this.$message({
            type: "success",
            message: '' + data,
            duration: 1000
          });
        },
        updateSearch (word) {
          this.searchParam.word = word;
          this.$message({
            type: "success",
            message: ': ' + word,
            duration: 1000
          });
        }
      }
    }
</script>
<style scoped>
  .search-ct {
    margin: 20px auto;
  }
</style>



