<template>
  <div class="">
    <h2>Pop</h2>
    <p></p>
    <div class="pop-tip-ct">
      <Poptip trigger="hover" title="" content="">
        <jo-button type="main">hover</jo-button>
      </Poptip>

      <Poptip title="" content="">
          <jo-button type="main">click</jo-button>
      </Poptip>

      <Poptip trigger="focus" title="" content="">
          <jo-button type="main">focus</jo-button>
      </Poptip>

      <Poptip
          confirm
          title=""
          @on-ok="ok"
          @on-cancel="cancel">
          <jo-button type="main"></jo-button>
      </Poptip>
    </div>
    
    <h3></h3>
    <div class="pop-tip-ct">
      <Poptip placement="top" trigger="hover" title="" content="">
        <jo-button type="main"></jo-button>
      </Poptip>

      <Poptip placement="right" trigger="hover" title="" content="">
          <jo-button type="main"></jo-button>
      </Poptip>

      <Poptip placement="bottom" trigger="hover" title="" content="">
          <jo-button type="main"></jo-button>
      </Poptip>

      <Poptip placement="left" trigger="hover" title="" content="">
          <jo-button type="main"></jo-button>
      </Poptip>
    </div>



    <p></p>
  
    <pre>
        <code class="language-html">
          
          &lt;template&gt;
              &lt;Poptip trigger="hover" title="" content=""&gt;
                &lt;jo-button type="main"&gt;hover&lt;/jo-button&gt;
              &lt;/Poptip>

              &lt;Poptip title="" content=""&gt;
                  &lt;jo-button type="main"&gt;click&lt;/jo-button&gt;
              &lt;/Poptip>

              &lt;Poptip trigger="focus" title="" content=""&gt;
                  &lt;jo-button type="main"&gt;focus&lt;/jo-button&gt;
              &lt;/Poptip>

              &lt;Poptip
                  confirm
                  title=""
                  @on-ok="ok"
                  @on-cancel="cancel"&gt;
                  &lt;jo-button type="main"&gt;&lt;/jo-button&gt;
              &lt;/Poptip>
              &lt;jo-button type="main" @click.left="openMsg()""&gt;&lt;/jo-button&gt;
          &lt;/template&gt; 
  
          &lt;script&gt;
            export default {
              methods: {
                ok () {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                },
                cancel () {
                  this.$message({
                    type: "info",
                    message: ""
                  });
                }
              }
            }
          &lt;/script&gt;
  
        </code>
    </pre>

    <h3>Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>trigger</td>
        <td>hoverclickfocus, confirm  click </td>
        <td>string</td>
        <td>click</td>
      </tr>
      <tr>
        <td>title</td>
        <td></td>
        <td>string|number</td>
        <td></td>
      </tr>
      <tr>
        <td>content</td>
        <td> confirm </td>
        <td>string|number</td>
        <td></td>
      </tr>
      <tr>
        <td>placement</td>
        <td>toptop-starttop-endbottombottom-startbottom-endleftleft-startleft-endrightright-startright-end</td>
        <td>string</td>
        <td>top</td>
      </tr>
      <tr>
        <td>width</td>
        <td> 150px confirm  300px</td>
        <td>number</td>
        <td></td>
      </tr>
      <tr>
        <td>confirm</td>
        <td></td>
        <td>boolean</td>
        <td>false</td>
      </tr>
      <tr>
        <td>ok-text</td>
        <td> confirm </td>
        <td>string</td>
        <td></td>
      </tr>
      <tr>
        <td>cancel-text</td>
        <td> confirm </td>
        <td>string</td>
        <td></td>
      </tr>
    </table>

  </div>
</template>

<script>
  'use strict'
  export default {
    name: 'Pop',
    data() {
      return {

        }
    },
    created() {
      HlInit();
    },
    methods: {
      ok () {
        this.$message({
          type: "success",
          message: ""
        });
      },
      cancel () {
        this.$message({
          type: "info",
          message: ""
        });
      }
    }
  }
</script>

<style scoped lang="scss">
.pop-tip-ct {
  margin: 20px auto;
}
</style>


