<template>
  <div class="">
    <h2></h2>
    <p> Notification </p>
  
    <h3></h3>
    <p>3 </p>
    <div class="top-msg-ct">
      <jo-button type="main" @click.left="openMsg()"></jo-button>
    </div>

    <p></p>
  
    <pre>
        <code class="language-html">
          
          &lt;template&gt;
            &lt;div id="foo-ct"&gt;
              &lt;jo-button type="main" @click.left="openMsg()""&gt;&lt;/jo-button&gt;
            &lt;/div&gt;
          &lt;/template&gt; 
  
          &lt;script&gt;
            export default {
              methods: {
                openMsg () {
                  this.$message('');
                }
              }
            }
          &lt;/script&gt;
  
        </code>
    </pre>
  
    <h3></h3>
    <p></p>
    <div class="top-msg-ct">
      <jo-button type="main" @click.left="topMsg()"></jo-button>
    </div>

    <pre>
        <code class="language-html">
          
          &lt;template&gt;
            &lt;div id="foo-ct"&gt;
              &lt;jo-button type="main" @click.left="topMsg()"&gt;&lt;/jo-button&gt;
            &lt;/div&gt;ShowMsg
          &lt;/template&gt;  
  
          &lt;script&gt;
            export default {
              methods: {
                topMsg () {
                  this.$message({
                    message: '',
                    type: 'success',
                    duration: 1000,
                    showClose: true,
                    onClose: function () {
                      window.alert("");
                    }
                  });
                }
              }
            }
          &lt;/script&gt;
  
        </code>
      </pre>
    
    <h3></h3>
    <p></p>
    
  
    <div class="top-msg-ct">
      <jo-button type="main" @click.left="ShowMsg('success')"></jo-button>
      <jo-button type="main" @click.left="ShowMsg('warning')"></jo-button>
      <jo-button type="main" @click.left="ShowMsg('error')"></jo-button>
    </div>
    <p></p>
  
    <pre>
        <code class="language-html">
          
          &lt;template&gt;
            &lt;div id="foo-ct"&gt;
              &lt;jo-button type="main" @click.left="ShowMsg('success')"&gt;&lt;/jo-button&gt;
              &lt;jo-button type="main" @click.left="ShowMsg('warning')"&gt;&lt;/jo-button&gt;
              &lt;jo-button type="main" @click.left="openMsg('error')"&gt;&lt;/jo-button&gt;
            &lt;/div&gt;ShowMsg
          &lt;/template&gt;  
  
          &lt;script&gt;
            export default {
              methods: {
                ShowMsg (type) {
                  switch (type) {
                    case 'success':
                      this.$message({
                        message: '',
                        type: 'success'
                      });
                      break;
                    case 'warning':
                      this.$message({
                        message: '',
                        type: 'warning'
                      });
                      break;
                    case 'error': 
                      this.$message({
                        message: '',
                        type: 'error'
                      });  
                    default:
                      break;
                  }
                }
              }
            }
          &lt;/script&gt;
  
        </code>
      </pre>
  
    <h3></h3>
    <p></p>
    <div class="top-msg-ct">
      <jo-button type="main" @click.left="openMsgClose('info')"></jo-button>
      <jo-button type="main" @click.left="openMsgClose('success')"></jo-button>
      <jo-button type="main" @click.left="openMsgClose('warning')"></jo-button>
      <jo-button type="main" @click.left="openMsgClose('error')"></jo-button>
    </div>

    <h3></h3>
    <p>Jo Vue.prototype  $message vue instance  Message</p>

    <h3>Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>message</td>
        <td></td>
        <td>string</td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>type</td>
        <td></td>
        <td>string</td>
        <td>success/warning/info/error</td>
        <td>info</td>
      </tr>
      <tr>
        <td>iconClass</td>
        <td> type</td>
        <td>string</td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>customClass</td>
        <td></td>
        <td>string</td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td>duration</td>
        <td>,  0 </td>
        <td>number</td>
        <td></td>
        <td>3000</td>
      </tr>
      <tr>
        <td>showClose</td>
        <td></td>
        <td>boolean</td>
        <td></td>
        <td>false</td>
      </tr>
      <tr>
        <td>onClose</td>
        <td>,  message </td>
        <td>function</td>
        <td></td>
        <td></td>
      </tr>
    </table>
  </div>
</template>

<script>
  'use strict'
  export default {
    name: 'Topmsg',
    data() {
      return {
      }
    },
    created() {
      HlInit();
    },
    methods: {
      openMsg () {
        this.$message('');
      },
      topMsg () {
        this.$message({
          message: '',
          type: 'success',
          duration: 1000,
          showClose: true,
          onClose: function () {
            window.alert("");
          }
        });
      },
      ShowMsg (type) {
        switch (type) {
          case 'success':
            this.$message({
              message: '',
              type: 'success'
            });
            break;
          case 'warning':
            this.$message({
              message: '',
              type: 'warning'
            });
            break;
          case 'error': 
            this.$message({
              message: '',
              type: 'error'
            });  
          default:
            break;
        }
      },
      openMsgClose (type) {
        switch (type) {
          case 'info':
            this.$message({
              message: '',
              type: 'info',
              showClose: true
            });
            break;
          case 'success':
            this.$message({
              message: '',
              type: 'success',
              showClose: true
            });
            break;
          case 'warning':
            this.$message({
              message: '',
              type: 'warning',
              showClose: true
            });
            break;
          case 'error': 
            this.$message({
              message: '',
              type: 'error',
              showClose: true,
            });  
          default:
            break;
        }
      }
    }
  }
</script>

<style scoped lang="scss">
.top-msg-ct {
  margin: 20px auto;
}
</style>


