<template lang="html">
    <div class="">
        <h2></h2>
        <p></p>
        <h3></h3>
        <p>v-modelboolean</p>
        <p>
            <jo-checkbox v-model="checkbox" label="true" name="doc">1</jo-checkbox>
        </p>
        <p>model{{checkbox}}</p>
        <p></p>
        <pre>
            <code class="language-html">
            &lt;template&gt;
                &lt;jo-checkbox v-model="checkbox" label="true" name="doc"&gt;1&lt;/jo-checkbox&gt;
            &lt;/template&gt;

            &lt;script&gt;
                export default {
                    data() {
                        return {
                            checkbox: true
                        }
                    }
                }
            &lt;/script&gt;
            </code>
        </pre>
        <p>v-modelarrayarray</p>
        <p>
            <jo-checkbox v-model="checkbox1" label="1" name="doc">1</jo-checkbox>
            <jo-checkbox v-model="checkbox1" label="2" name="doc">2</jo-checkbox>
            <jo-checkbox v-model="checkbox1" label="3" name="doc">3</jo-checkbox>
            <jo-checkbox v-model="checkbox1" label="4" name="doc">4</jo-checkbox>
        </p>
        <p>model{{checkbox1}}</p>
        <p></p>
        <pre>
            <code class="language-html">
            &lt;template&gt;
                &lt;jo-checkbox v-model="checkbox1" label="1" name="doc"&gt;1&lt;/jo-checkbox&gt;
                &lt;jo-checkbox v-model="checkbox1" label="2" name="doc"&gt;2&lt;/jo-checkbox&gt;
                &lt;jo-checkbox v-model="checkbox1" label="3" name="doc"&gt;3&lt;/jo-checkbox&gt;
                &lt;jo-checkbox v-model="checkbox1" label="4" name="doc"&gt;4&lt;/jo-checkbox&gt;
            &lt;/template&gt;

            &lt;script&gt;
                export default {
                    data() {
                        return {
                            checkbox1: ['1', '3']
                        }
                    }
                }
            &lt;/script&gt;
            </code>
        </pre>
        <h3></h3>
        <p></p>
        <p>
            <jo-checkbox v-model="checkbox2" label="1" name="doc">1</jo-checkbox>
            <jo-checkbox v-model="checkbox2" label="3" name="doc" disabled>2</jo-checkbox>
            <jo-checkbox v-model="checkbox2" label="4" name="doc" disabled>3</jo-checkbox>
        </p>
        <p>model{{checkbox2}}</p>
        <p></p>
        <pre>
            <code class="language-html">
            &lt;template&gt;
                &lt;jo-checkbox v-model="checkbox2" label="1" name="doc"&gt;1&lt;/jo-checkbox&gt;
                &lt;jo-checkbox v-model="checkbox2" label="3" name="doc" disabled;&gt;2&lt;/jo-checkbox&gt;
                &lt;jo-checkbox v-model="checkbox2" label="4" name="doc" disabled&gt;3&lt;/jo-checkbox&gt;
            &lt;/template&gt;

            &lt;script&gt;
                export default {
                    data() {
                        return {
                            checkbox2: ['1', '3']
                        }
                    }
                }
            &lt;/script&gt;
            </code>
        </pre>
        <h3></h3>
        <table>
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>label</td>
                <td>Checkboxvalue</td>
                <td>string,number,boolean</td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <td>name</td>
                <td>Checkboxname</td>
                <td>string</td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <td>disabled</td>
                <td></td>
                <td>boolean</td>
                <td></td>
                <td>false</td>
            </tr>
        </table>
    </div>
</template>

<script>
    'use strict'

    export default {
        name: 'checkbox',
        components: {

        },
        data() {
            return {
                checkbox: true,
                checkbox1: ['1', '3'],
                checkbox2: ['1', '3']
            }
        },
        created() {
            HlInit()
        }
    }
</script>



