<template>
    <div class="">
        <h2></h2>
        <p></p>
        <p>
            <jo-button type="main" @click="show=true">Dialog</jo-button>
        </p>
        <p>
            <jo-dialog :show="show" @close="show=false">
                <div slot="info" style="text-align:center;">
                    
                </div>
                <div slot="btns" class="dialog-btns">
                    <jo-button @click.left="show=false" type="main"></jo-button>
                    <span style="display: inline-block; height: 0px; width: 10px"></span>
                    <jo-button @click.left="show=false"></jo-button>
                </div>
            </jo-dialog>
        </p>
    </div>
</template>

<script>
    'use strict'

    export default {
        name: 'dialog',
        components: {

        },
        data() {
            return {
                show: false
            }
        },
        created() {
            HlInit()
        },
        methods : {

        }
    }
</script>



