<template>
  <div class="">
    <h2></h2>
    <p></p>
    <h3></h3>
    <p>loading</p>
    <div id="foo-ct">
      <jo-loading :loading="loading"></jo-loading>
    </div>
    <jo-button type="main" @click.left="toggleLoding()">/</jo-button>
    <jo-button type="main" @click.left="tryFull()"></jo-button>
    <jo-button type="main" @click.left="addText()"></jo-button>
    <p></p>

    <pre>
      <code class="language-html">
        
        &lt;template&gt;
          &lt;div id="foo-ct"&gt;
            &lt;jo-loading :loading="loading"&gt;&lt;/jo-loading&gt;
          &lt;/div&gt;
        &lt;/template&gt; 

        &lt;script&gt;
          export default {
            data() {
              return {
                loading: {
                  'text': null,
                  'fullscreen': false,
                  'visible': true,
                  'customClass': 'bar'
                }
              }
            }
          }
        &lt;/script&gt;

      </code>
    </pre>
    <h3>Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>text</td>
        <td></td>
        <td>string</td>
        <td>null</td>
      </tr>
      <tr>
        <td>fullscreen</td>
        <td>Loading target</td>
        <td>boolean</td>
        <td>true</td>
      </tr>
      <tr>
        <td>visible</td>
        <td>Loading </td>
        <td>boolean</td>
        <td>false</td>
      </tr>
      <tr>
        <td>customClass</td>
        <td>Loading </td>
        <td>string</td>
        <td></td>
      </tr>
    </table>
  </div>
</template>

<script>
  'use strict'
  export default {
    name: 'Loading',
    data() {
      return {
        loading: {
          'text': null,
          'fullscreen': false,
          'visible': true,
          'customClass': 'bar'
        }
      }
    },
    created() {
      HlInit()
    },
    methods: {
      tryFull () {
        let _this = this;
        this.loading.fullscreen = true;
        this.loading.visible = true;
        setTimeout( ()=> {
          _this.loading.fullscreen = false;
        }, 2000);
      },
      toggleLoding () {
       this.loading.visible = !this.loading.visible;
      },
      addText () {
        this.loading.text = ""
      }
    }
  }
</script>

<style scoped lang="scss">
  #foo-ct {
    margin-bottom: 10px;
    position: relative;
    height: 250px;
    background-color: #f9fafc;
    border-radius: 4px;
    border: 1px solid #eaeefb;
  }
</style>



