<template>
  <div class="">
    <h2></h2>
    <p></p>
    <h3></h3>
    <p></p>
    <div class="select-ct">
      <jo-select :disable= "select.disable" :width = "select.width" :options="select.options" :name="select.name" @select-change="updateSelect"></jo-select>
    </div>

    <jo-button type="main" @click.left="select.width = 260"></jo-button>
    <jo-button type="main" @click.left="select.width = 100"></jo-button>
    <jo-button type="main" @click.left="select.width = 300"></jo-button>
    <jo-button type="main" @click.left="select.disable = !select.disable">/</jo-button>
    
    <p></p>

    <pre>
      <code class="language-html">    
        
        &lt;template&gt;
          &lt;div class="select-ct"&gt;
            &lt;jo-select :disable= "select.disable" 
                      :width = "select.width" 
                      :options="select.options" 
                      :name="select.name"  @select-change="updateSelect"&gt;
            &lt;/jo-select&gt;
          &lt;/div&gt;
        &lt;/template&gt; 

        &lt;script&gt;
          export default {
            data&nbsp;() {
              return {
                select: {
                  disable: false,
                  width: 260,
                  name: "name",
                  options: []
                }
              }
            },
            methods: {
              updateSelect (val) {
                this.$message({
                  type: "success",
                  message: '' + (val.index + 1) + ''
                })
              }
            }
          }
        &lt;/script&gt;

      </code>
    </pre>
    <h3>Options</h3>
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>disable</td>
        <td></td>
        <td>boolean</td>
        <td>false</td>
      </tr>
      <tr>
        <td>width</td>
        <td></td>
        <td>number</td>
        <td>260</td>
      </tr>
      <tr>
        <td>name</td>
        <td>Key</td>
        <td>string</td>
        <td>-</td>
      </tr>
      <tr>
        <td>options</td>
        <td></td>
        <td>array</td>
        <td></td>
      </tr>
    </table>
  </div>
</template>

<script>
  'use strict'
  export default {
    name: 'Select',
    data() {
      return {
        select: {
          disable: false,
          width: 260,
          name: "val",
          options: [
            {
            "val": "1123123123123",
            "id": "1",
            "price": 1
            },
            {
              "val": "2123123123123",
              "id": "2",
              "price": 2
            },
            {
              "val": "31231231231",
              "id": "3",
              "price": 3
            },
            {
              "val": "412312312312",
              "id": "4",
              "price": 4
            },
            {
            "val": "112312312312",
            "id": "1",
            "price": 1
            },
            {
              "val": "21231231231",
              "id": "2",
              "price": 2
            },
            {
              "val": "3121231231",
              "id": "3",
              "price": 3
            },
            {
              "val": "412312312",
              "id": "4",
              "price": 4
            }
          ]
        }
      }
    },
    created() {
      HlInit()
    },
    methods: {
      updateSelect (val) {
        this.$message({
          type: "success",
          message: '' + (val.index + 1) + ''
        });
      }
    }
  }
</script>

<style scoped lang="scss">
  .select-ct {
    margin: 20px auto;
  }
</style>



